body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
  font-family: "Tiempos", Times, serif;
}

h1 {
  text-align: center;
}

.logo {
  width: 180px;
  margin: 10px;
}

.headimg {
  width: 100%;
  z-index: -1;
}

.container {
  z-index: 1;
  position: absolute;
  width: 100%;
}

.container h1 {
  font-size: 40px;
  margin: 4px;
}

.text {
  text-align: center;
  width: 40%;
  margin: 10% auto;
  padding: 35px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
}

.btn {
  background-color: #FF6D00;
  color: white;
  padding: 10px;
  margin-top: 20px;
  border: none;
  border-radius: 7px;
  cursor: pointer;
  font-family: "Comic Sans MS";

}

.btnn:hover,
.btn:hover {
  background-color: #d96710;
}

.button {
  float: right;
  margin: 15px 25px;
  display: block;
}

.text .btn {
  font-size: 20px;
}

.orange {
  color: #FF6D00;
}

.para {
  text-align: center;
  padding: 10px 22%;
}

.section3 {
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.sect {
  text-align: center;
  font-size: 11px;
}

.sect .box {
  margin: 5% auto;
  padding: 40px;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: url('./Images/dotsbg.png');
  box-shadow: 7px 7px 14px #888888;
}

.section3 .box:hover {
  margin: 0% auto;
  width: 220px;
  height: 220px;
}

.boximg {
  font-size: 70px;
  color: #FF6D00;
  padding: 0px;
  margin: 0px;
}


.div {
  display: grid;
  background: url('./Images/dotsbg.png');
  margin: 10px 0px;
  grid-template-columns: repeat(2, 1fr);
}

.imagel,
.imager {
  width: 200px;
  display: block;
  margin: 18% auto;
}

.parag {
  padding: 120px 70px;
  font-size: 130%;
}

.align {
  vertical-align: middle;
}

.footer {
  text-align: center;
  background-color: rgb(65, 65, 65);
  color: #fff;
  padding: 7px;
}

.section1 {
  margin: 40px 12%;
}

.workimg {
  width: 100%;
  margin: auto;
  display: block;
  box-shadow: 5px 10px 18px #888888;
  margin-top: 30px;
}

label {
  color: #FF6D00;
  font-weight: bolder;
  font-size: 18px;
}

.form {
  width: 62%;
  display: block;
  margin: 30px auto;
  padding: 30px;
  border-radius: 20px;
  background-color: #e4e4e4;
}

input {
  display: block;
  margin: 10px 0px 20px 0px;
  padding: 10px;
  width: 90%;
}

.form h1 {
  text-align: left;
}

.buttonn {
  margin: auto;
  display: table;
}

.btnn {
  background-color: #FF6D00;
  border: none;
  color: #fff;
  padding: 0px 10px;
  margin: 4px;
  text-align: center;
  cursor: pointer;
  font: 12px "Comic Sans MS";
}

.link{
  text-decoration: none;
  color: #fff;
}

.form h3 {
  color: #888888;
  font-size: 16px;
}

.form .logo {
  display: block;
  margin: auto;
}

.navbar {
  position: sticky;
  top: 0px;
  z-index: 2;
  background-color: #fff;
}

.imagel {
  animation: rotatel 20s infinite;
}

@keyframes rotatel {
  0% {
    transform: rotate(-15deg)
  }

  50% {
    transform: rotate(15deg)
  }

  100% {
    transform: rotate(-15deg)
  }
}

.imager {
  animation: rotater 20s infinite;
}

@keyframes rotater {
  0% {
    transform: rotate(15deg)
  }

  50% {
    transform: rotate(-15deg)
  }

  100% {
    transform: rotate(15deg)
  }
}


@media (max-width: 900px) {
  .container h1 {
    font-size: 25px;
  }

  .text .btn {
    padding: 10px;
    font-size: 18px;
  }

  .text {
    padding: 15px;
    margin: 5% auto;
  }

  .section3 {
    display: block;
  }
}

@media(max-width: 550px) {

  .imagel,
  .imager {
    width: 100px;
  }

  .parag {
    font-size: 80%;
    padding: 40px 20px;
  }

  .para {
    font-size: 11px;
  }

  h1 {
    font-size: 22px;
  }

  .container h1 {
    font-size: 18px;
  }

  .btn {
    margin-top: 8px;
    font-size: 12px;
    padding: 7px;
  }

  .text .btn {
    font-size: 14px;
  }

  .logo {
    width: 120px;
  }

  .sect .box {
    margin: 20px auto;
    padding: 40px;
    width: 120px;
    height: 120px;
    font-size: 9px;
  }

  .section3 .box:hover {
    margin: 10px auto;
    width: 140px;
    height: 140px;
  }

  .boximg {
    font-size: 40px;
    color: #FF6D00;
    padding: 0px;
    margin: 0px;
  }
}

@media (max-width: 350px) {

  .imagel,
  .imager {
    width: 60px;
  }

  .logo {
    width: 100px;
  }

  .parag {
    font-size: 50%;
    padding: 18px;
  }

  .container h1 {
    font-size: 14px;
  }

  .text .btn {
    padding: 7px;
    font-size: 8px;
  }

  .btn {
    font-size: 8px;
    padding: 5px;
  }

  .text {
    padding: 10px;
    margin: 5% auto;
  }

  .button {
    margin-top: 10px;
    font-size: 10px;
  }

  .footer {
    font-size: 12px;
  }
}